export default function ImageCarousel({ images = [] }) {
    return (
        <div className="h-[25vh] snap-mandatory snap-x flex gap-4 overflow-x-auto">
            {
                images.map((item, index) => {
                    return (
                        <div key={index} className={`${images.length == 1 ? 'min-w-[100%]' : 'min-w-[85%]'} h-full bg-stone-200 rounded-2xl snap-center relative overflow-hidden`}>
                            <img src={item} className="w-full h-full object-cover"/>
                        </div>
                    )
                })
            }
        </div>
    )
}