import lodash from 'lodash';
import Lib from './lib';




const chainLinkPlanUp = (curPlan, plans, data = []) => {
    const linkedPlan = plans.filter(p => p.id == curPlan.linkPlanId)[0];
    if (linkedPlan) {
        return chainLinkPlanUp(linkedPlan, plans, [curPlan, ...data]);
    }
    return [curPlan, ...data];
}


const chainLinkPlanDown = (curPlan, plans, data = []) => {
    const linkedPlan = plans.filter(p => p.linkPlanId == curPlan.id)[0];
    if (linkedPlan) {
        return chainLinkPlanUp(linkedPlan, plans, [...data, curPlan]);
    }
    return [...data, curPlan];
}

const calc = (plan, amount) => {
    if (plan.type == 'rate') {
        return amount * plan.value / 100;
    } else if (plan.type == 'value') {
        return plan.value;
    }
    return 0;
}


const BookingCalcLib = {
    /**
     * Calculate Each Plan amount
     * #1 plan -> discount -10,000
     * 
     * @param {Plan} curPlan 
     * @param {Number} amount 
     * @param {[Plans]} plans 
     * @returns {Number}
     */
    calcPlanAmount: (curPlan, amount, plans, debug = false) => {

        let amt = curPlan.type == 'sale' ? curPlan.value : amount;
        let lastDeductedValue = null;
        let totalDeductedAmount = 0;
        const planChain = chainLinkPlanUp(curPlan, plans);
        planChain.forEach((plan, index) => {
            lastDeductedValue = calc(plan, amt);
            totalDeductedAmount = totalDeductedAmount + lastDeductedValue;
            amt = amt - lastDeductedValue;
        })

        return { 
            amount: curPlan.type == 'sale' ? curPlan.value : amount, 
            lastDeductedValue 
        };
    },
    /**
     * 
     * Calculate Each Room conclused amount
     * 
     * @param {Room} room 
     * @param {{
     *  adult: Number,
     *  child: Number,
     *  nights: Number,
     *  coupon: Number
     * }} param1 
     * @returns {
     *  amount: Number,
     *  amountBeforeDiscount: Number,
     *  totalDiscount: Number,
     *  freeStays: Number
     * }
     */
    calculateRoomPlan(room, { adult = 1, child = 1, nights = 1, coupon = null } = {}) {
        let amount = room.amount;
        const plans = room.plans || [];
        const discounts = [];
        let freeStays = 0;


        if (plans.length == 0) {
            return {
                amount: amount,
                amountBeforeDiscount: amount,
                totalDiscount: 0,
                freeStays,
            }
        }



        plans.forEach(plan => {
            const { amount: amt, lastDeductedValue } = BookingCalcLib.calcPlanAmount(plan, amount, plans);
            amount = amt;
            discounts.push(lastDeductedValue);
        })


        const totalDiscount = discounts.reduce((prev, cur) => prev + cur, 0);
        const finalAmount = amount - totalDiscount;

        return {
            amount: finalAmount <= 0 ? 0 : finalAmount,
            amountBeforeDiscount: amount,
            totalDiscount,
            freeStays,
        }
    },
    /**
     * Calculate Tax 
     * @param {Tax} param0 
     * @returns {
     *  total: Number,
     *  totalRate: Number
     * }
     */
    calculateTax({ taxs = [], amount = 0 } = {}) {
        return taxs.reduce((prev, curTax) => {
            const taxAmt = curTax.type == 'rate' ? (amount * (curTax.value / 100)) : curTax.value;
            return {
                totalRate: prev.totalRate + (curTax.type == 'rate' ? curTax.value : 0),
                total: prev.total + taxAmt
            }
        }, { total: 0, totalRate: 0 })
    },
    /**
     * 
     * Calculate Reservation conclused amount
     * 
     * @param {Reservation} reservation 
     * @returns 
     */
    calculateReservationCost(reservation) {
        const nights = Lib.getNightsBetweenDates(reservation.checkOutDate, reservation.checkInDate);
        const { adult, child, coupon } = reservation;
        const roomsPrice = reservation.rooms.reduce((prev, curRoom) => {
            const planAmount = BookingCalcLib.calculateRoomPlan(curRoom, { adult, child, nights, coupon })
            const count = curRoom.reservedRoomCount;

            return {
                totalCount: prev.totalCount + count,
                adult: prev.adult + (count * curRoom.adult),
                child: prev.child + (count * curRoom.child),
                amountPerNight: prev.amountPerNight + (count * curRoom.amount),
                amount: prev.amount + (count * curRoom.amount * nights), // amount before discount
                totalDiscount: prev.totalDiscount + (count * planAmount.totalDiscount * nights), // total discount
                amountWithPlan: prev.amountWithPlan + (count * planAmount.amount * nights), // amount after plan 
                freeStay: Math.max(prev.freeStay, planAmount.freeStays)
            }
        }, {
            adult: 0,
            child: 0,
            amount: 0,
            totalCount: 0,
            amountWithPlan: 0,
            totalDiscount: 0,
            amountPerNight: 0,
            freeStay: 0
        });

        const taxs = BookingCalcLib.calculateTax({ taxs: reservation.taxs, amount: roomsPrice.amount })


        return {
            roomsPrice,
            taxs
        }
    }
}

export default BookingCalcLib;