import { Plus, Minus } from '@icon-park/react';
import { useNavigate } from 'react-router-dom';
import { routes } from '../App';
import { useBookingContext } from '../context/booking-context';
import BookingCalcLib from './../lib/booking-lib';

export default function RoomCard({ room }){

    const navigate = useNavigate();
    const { booking, setBooking } = useBookingContext();
    const planPrice = BookingCalcLib.calculateRoomPlan(room);

    const add = (event) => {
        event.stopPropagation();
        setBooking(b => {
            const newBooking = {...b};
            if(!newBooking.rooms[room.id]){
                newBooking.rooms[room.id] = { count: 1 };
            }else{
                newBooking.rooms[room.id].count = (newBooking.rooms[room.id]?.count ?? 0) + 1;
            }
            return newBooking;
        })
    }

    const minus = (event) => {
        event.stopPropagation();
        setBooking(b => {
            const newBooking = {...b};
            if(!newBooking.rooms[room.id]){
                newBooking.rooms[room.id] = { count: 0 };
            }else{
                newBooking.rooms[room.id].count = (newBooking.rooms[room.id]?.count ?? 0) - 1;
                if(newBooking.rooms[room.id].count <= 0) newBooking.rooms[room.id].count = 0;
            }
            return newBooking;
        })
    }   


    return (
        <div 
        onClick={() => navigate(routes.RoomDetail.path, { state: { room } })}
        className="rounded-2xl bg-primary overflow-hidden shadow-lg shadow-black/5">
            <div className="h-[10rem] relative">
                <img src={room.images[0]} className='w-full h-full object-cover'/>
            </div>
            <div className="p-3 bg-gray-50 flex justify-between items-center">
                <div className="flex flex-col">
                    <div className="font-bold">{room.name}</div>
                    <div className='flex items-center gap-1'>
                        <div className="font-light text-[0.9rem] opacity-70">{`${planPrice.amount.toLocaleString()} MMK`}</div>
                        {room.amount != planPrice.amount && <div className="font-light text-[0.8rem] opacity-50 line-through">{`${room.amount.toLocaleString()} MMK`}</div>}
                    </div>
                    <div className="font-light text-[0.85rem] opacity-50">{`${room.adult} Adult, ${room.child} Child`}</div>
                </div>
                <div className='flex gap-3 items-center'>
                    <div className='p-1 border rounded-full' onClick={add}><Plus theme="outline" size="20"/></div>
                    <div className='font-bold select-none'>{booking.rooms[room.id]?.count || 0}</div>
                    <div className='p-1 border rounded-full' onClick={minus}><Minus theme="outline" size="20"/></div>
                </div>
            </div>
        </div>
    )
}


