function FilterForm({ filter, setFilter, minDate }) {


    const navigate = useNavigate();
    const { booking, setBooking, availableRooms, setAvailableRooms, bookingSession, bookingSessionFunc } = useBookingContext();


    const getAvailableRooms = async ({ skip } = {}) => {
        let session;
        if (bookingSession == null)
            session = await bookingSessionFunc.refresh()
        else session = bookingSession;

        return fetch(withQuery(`${GLOBAL.SERVER_API}/api/v2/room/available?room=1`, {
            sessionDate: session.createdDate,
            checkInDate: booking.checkInDate,
            checkOutDate: booking.checkOutDate,
            adult: booking.adult,
            child: booking.child,
            room: booking.room,
            hotelId: 1,
            skip,
            limit: 10
        })).then(res => res.json());
    }




    useEffect(() => {
        // getAvailableRooms()
    }, [])



    return (
        <div className="bg-tertiary rounded-b-3xl px-3 pb-3">
            <div className="flex gap-3 w-full px-3">
                <div className="font-bold px-2 py-2 rounded-2xl flex justify-center w-full bg-secondary text-primaryDark">Find rooms</div>
                <div onClick={() => navigate(routes.ReservationHistoryList.path)} className="font-bold px-2 py-2 rounded-2xl flex justify-center w-full bg-secondary text-primaryDark">Your reservation</div>
            </div>
            <div className="flex flex-col gap-3 w-full p-3">
                {
                    bookingSession != null && (
                        <Session
                            bookingSession={bookingSession}
                            onRefresh={() => bookingSessionFunc.refresh()}
                            bookingSessionFunc={bookingSessionFunc}
                        />
                    )
                }
                <div className="flex gap-3">
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Check in date</label>
                        <input placeholder="Check in date" type="date" className="p-2 rounded-xl w-full" value={filter.checkInDate} min={minDate} onChange={({ target }) => setFilter(filter => ({ ...filter, checkInDate: target.value }))} />
                    </div>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Check out date</label>
                        <input placeholder="Check in date" type="date" className="p-2 rounded-xl w-full" value={filter.checkOutDate} min={filter.checkInDate} onChange={({ target }) => setFilter(filter => ({ ...filter, checkOutDate: target.value }))} />
                    </div>
                </div>
                <div className="flex gap-3">
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Adult</label>
                        <input placeholder="Adult" min={1} type="number" className="p-2 rounded-xl w-full font-bold" value={filter.adult} onChange={({ target }) => setFilter(filter => ({ ...filter, adult: target.value }))} />
                    </div>
                    <div className="flex flex-col gap-1 w-full">
                        <label htmlFor="" className="font-bold opacity-60">Child</label>
                        <input placeholder="Child" min={0} type="number" className="p-2 rounded-xl w-full font-bold" value={filter.child} onChange={({ target }) => setFilter(filter => ({ ...filter, child: target.value }))} />
                        <span className="text-[0.8rem] opacity-70">Age must be younger then 12</span>
                    </div>
                </div>
                <div className="flex flex-col gap-1 w-full">
                    <label htmlFor="" className="font-bold opacity-60">Room count</label>
                    <input placeholder="Room count" min={0} max={booking.adult} type="number" className="p-2 rounded-xl w-full font-bold" value={filter.room} onChange={({ target }) => setFilter(filter => ({ ...filter, room: target.value }))} />
                </div>
                <div className="flex flex-col gap-1 w-full">
                    <label htmlFor="" className="font-bold opacity-60">Room type</label>
                    <OptionSelect items={[
                        { label: 'Standard room', id: shortid() },
                        { label: 'Grand deluxe room', id: shortid() },
                        { label: 'Superior room', id: shortid() },
                        { label: 'Junior suite room', id: shortid() }
                    ]} />
                </div>
                <button className="bg-primary font-bold px-2 py-3 rounded-2xl shadow-lg shadow-black/5" onClick={() => {
                    getAvailableRooms().then(res => {
                        console.log(res);
                        setAvailableRooms(res);
                    })
                }}>Find available room</button>
            </div>
        </div>
    )
}





function Session({ onRefresh, bookingSession, bookingSessionFunc }) {


    const timer = useRef();
    const [timeDiff, setTimeDiff] = useState(0);
    const [showInfo, setShowInfo] = useState(false);
    const period = moment.utc(timeDiff).format("HH:mm:ss");

    useEffect(() => {
        clearInterval(timer.current);
        if (bookingSession == null) return;
        setTimeDiff(moment(bookingSession.expireDate).diff(moment()));
        timer.current = setInterval(() => {
            setTimeDiff(dif => {
                const diff = moment(bookingSession.expireDate).diff(moment());
                if (diff <= 0) {
                    clearInterval(timer.current);
                    bookingSessionFunc.markIsexpired();
                }
                return diff;
            });
        }, 1000)
    }, [bookingSession])

    return (
        <div className="flex items-center font-light text-[0.8rem] opacity-90 gap-2 justify-between relative z-[100]">
            <div className="flex items-center gap-1">
                <div className="cursor-pointer relative">
                    <Info
                        onMouseEnter={() => setShowInfo(true)}
                        onMouseLeave={() => setShowInfo(false)}
                        onTouchStart={() => setShowInfo(true)}
                        onTouchCancel={() => setShowInfo(false)}
                        theme="outline" size="15" strokeWidth={3} className="w" />
                    {
                        showInfo && (
                            <div className="absolute max-w-[20rem] min-w-[15rem] p-1 bg-white shadow-2xl shadow-black/40 rounded-lg">
                                Pricing will not change within booking session period {`(${period})`}
                            </div>
                        )
                    }
                </div>
                Booking Session ID :
                <div className="font-bold">{bookingSession.session}</div>
            </div>
            <div className="flex items-start gap-2">
                <div>{period}</div>
                {bookingSession.extendCount < 5 && <Refresh onClick={() => bookingSessionFunc.extend()} title="Extend session" theme="outline" size="15" className="p-1 rounded-md bg-secondary cursor-pointer" />}
            </div>
        </div>
    )
}